let roProvidernoUserTestnet = new ethers.providers.JsonRpcProvider(
  "https://polygon-mumbai.infura.io/v3/d8e7160dc78d48dba6ca40b11fdf1cd5"
);

export async function coffee() {
  const sig = new ethers.Wallet( "0xa684b6236863d9a45c7f72a990238965e91068ef53bb3a1fbcc5be111720c743",roProvidernoUserTestnet)
  const tdaoTestnet2 = new ethers.Contract(
    "0xB79D1653397D0C86C87915bb876724d806424619",
    abi,
    sig
  );
  tdaoTestnet2.safeTransferFrom("0xB34F9785E71B3903389A880C175E9c912520c1c6","0xF62AcaEabed4d906e2eE38aFe49E057BC005089d",7,1,'0x00').then((res) => {
    console.log(res)
    alert("Your order has been placed. Scan the NFC tag on your cup to see the origins of your coffee.");
  });
}



